import { URLSearchParams, Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';

const SERVER_URI = 'http://54.172.244.133/WEB-PRO/API/api/v1/';

@Injectable()
export class Service {
  params: URLSearchParams;

  constructor(public http: Http, private db: AngularFireDatabase) {
    this.params = new URLSearchParams();
  }

  login(phone) {
    return new Promise((resolve, reject) => {
      let list = this.db.list('users').subscribe(data => {
        let isExistUser = false;
        data.map(d => {
          if (d.phone == phone) {
            isExistUser = true;
            resolve({id: d.$key, registered: true});
          }
        });

        if (!isExistUser) {
          resolve({id: this.db.list('users').push({phone: phone, verified: false}).key, registered: false});
        }
      });
    });
  }

  updateUser(userId, data) {
    return new Promise((resolve, reject) => {
      this.db.list('users').update(userId, {type: data.type, name: data.name}).then(res => {
        resolve(res);
      });
    });
  }

  sendMessage(roomId, data) {
    return new Promise((resolve, reject) => {
      this.db.list('messages/' + roomId + '/messages').push(data).then(res => {
        resolve(res);
      });
    });
  }

  getRoomId(userId, phone, myPhone) {
    return new Promise((resolve, reject) => {
      let isExistRoom = false;
      let list = this.db.list('messages').subscribe(data => {
        data.map((d: any) => {
          if (d.room.indexOf(phone) >= 0 && d.room.indexOf(myPhone) >= 0) {
            isExistRoom = true;
            resolve(d.$key);
          }
        });

        if (!isExistRoom) {
          resolve(this.db.list('messages').push({room: phone + '-' + myPhone, userId: userId}).key);
        }
      });
    });
  }

  getProviders() {
    return new Promise((resolve, reject) => {
      this.db.list('users').subscribe(data => {
        let providers = [];
        data.map(d => {
          if (d.type !== 'Customer') {
            providers.push(d);
          }
        });

        resolve(providers);
      });
    });
  }

  getUserName(userId) {
    return new Promise((resolve, reject) => {
      this.db.list('users').subscribe(data => {
        data.map(d => {
          if (d.$key == userId) {
            resolve(d.name);
          }
        });
      });
    });
  }

  addMedication(data) {
    let url = SERVER_URI + 'addMedication';
    this.params = new URLSearchParams();
    this.params.append('user_number', data.number);
    this.params.append('medication_name', data.name);
    this.params.append('medication_strength', '');
    this.params.append('medication_interval', data.interval);
    this.params.append('medication_no_of_days', '');
    return new Promise((resolve, reject) => {
      this.http.post(url, {search: this.params}).subscribe(res => {
        console.log(res)
      });
    });
  }

  getMedication() {
    let url = SERVER_URI + 'getMedication';
    this.params = new URLSearchParams();
    this.params.append('user_number', localStorage.getItem('phone'));
    return new Promise((resolve, reject) => {
      this.http.get(url, {search: this.params}).subscribe(res => {
        console.log(res)
      });
    });
  }

  getConnectedProviders() {
    let url = SERVER_URI + 'get_connected_providers';
    this.params = new URLSearchParams();
    this.params.append('ph_no', localStorage.getItem('phone'));
    return new Promise((resolve, reject) => {
      this.http.get(url, {search: this.params}).subscribe(res => {
        console.log(res)
      });
    });
  }
}



